@import '../../../assets/scss/base';

.table {
    border-collapse: separate;
    border-spacing: 0 10px;
    color: $base-color;
    thead {
        th {
            background: $thead-color;
            font-weight: $font-semibold;
            text-transform: $capitalize;
            @include font-size (1);
            white-space: nowrap;
            color: $base-color;
            border: 0;
            padding: .70rem 1rem;
            &:first-child {
                @include border-radius(7px 0 0 7px);
            }
            &:last-child {
                @include border-radius(0 7px 7px 0);
            }
        }
    }
    tbody {
        tr {
            td {
                padding: .40rem 1rem;
                @include font-size (1);
                border-width: 1px 0;
                background: $grey-color-400;
                border-color: $grey-color-700;
                vertical-align: middle;
                position: $relative;
                white-space: nowrap;
                &:first-child {
                    border-left-width: 1px;
                    @include border-radius(7px 0 0 7px);
                }
                &:last-child {
                    border-right-width: 1px;
                    @include border-radius(0 7px 7px 0);
                    &::after {
                        display: $none;
                    }
                }
                &::after {
                    content: '';
                    position: $absolute;
                    right: 0;
                    top: 5px;
                    background: $grey-color-700;
                    height: calc(100% - 10px);
                    width: .5px;
                }
            }
            .btn-grey {
                background: $white-color;
            }
        }
        &:nth-child(even) {
            td {
                background: $white-color;
                .btn-grey {
                    background: $grey-color-400;
                }
            }
        }
        &:hover {
            td {
                background: $grey-color-300;
            }
        }
    }
}