@import '../../../assets/scss/base';

.breadcrumb {
    .breadcrumb-item {
        font-weight: $font-bold;
    }
}

.profile_pic {
    position: $relative;
}

.upload_icon{
    position: $absolute;
    background: $primary-color;
    @include border-radius (50%);
    padding: 2px;
    text-align: $center;
    @include flexCenter;
    width: 30px;
    height: 30px;
    right: 0;
    bottom: 0px;
    overflow: $hidden;
    z-index: 1;
    input[type="file"] {
        position: $absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        @include opacity (0);
        z-index: 1;
    }
    img {
        width: 15px;
    }
}